@import '../../stylesheets/variables';

.pokemon__list-item {
  display: block;
  background: $color-white;
  border: 10px solid $color-grey;
  box-sizing: border-box;
  border-radius: 5%;
  width: 250px;
  height: 300px;
  color: $color-white;
  margin: 10px auto;
  box-shadow: 2px 2px 5px $color-black;
  transition: transform 0.5s;

  &:hover{
    transform: scale(1.05);
  }
  
  .pokemon__list-link{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: $color-black;
  }
  
  .pokemon__image {
    width: 150px;
  }
  
  .pokemon__id {
    margin: 0;
  }
  
  .pokemon__name {
    margin: 0;
  }
}
.types__list {
  margin: 5px 0;
  width: 100%;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
}
.type{
  display: inline-block;
  padding: 2px 5px;
  border: 1px solid black;
  border-radius: 25%;
  &-poison {
    background-color: $color-purple;
  }
  
  &-grass {
    background-color: $color-green;
  }
  
  &-fire {
    background-color: $color-red-fire;
  }
  
  &-water {
    background-color: $color-blue-sea;
  }
  
  &-flying {
    background-color: $color-white;
    color: $color-blue-sky;
  }
  
  &-bug {
    background-color: $color-green-dark;
  }
  &-electric{
    background-color: $color-yellow;
  }
}
.evolution{
  margin: 5px 0 0 0;
  text-align: center;
}